.popup {
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0px;
  left: 0px;
  background: rgba(0, 0, 0, 0.75);
  text-align: center;
  z-index: 99999;
  overflow: hidden;
  overflow-y: auto;
	background-color: rgba(0, 0, 0, 0.6);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
}
.popup:before {
  content: "";
  display: inline-block;
  height: 100%;
  margin-right: -4px;
  vertical-align: middle;
}
.popup-inner {
  display: inline-block;
  text-align: left;
  vertical-align: middle;
  position: relative;
  max-width: 700px;
  width: 90%;
  padding: 40px;
  box-shadow: 0px 10px 75px 2px #ec5f42;
  border-radius: 3px;
  background: #ec5f42;
  text-align: center;
}
.popup-inner h1 {
  font-family: "Roboto Slab", serif;
  font-weight: 700;
}
.popup-inner p {
  font-size: 24px;
  font-weight: 400;
}
.popup-close {
  width: 34px;
  height: 34px;
  padding-top: 4px;
  display: inline-block;
  position: absolute;
  top: 30px;
  right: 35px;
  -webkit-transform: translate(50%, -50%);
  transform: translate(50%, -50%);
  border-radius: 100%;
  background: transparent;
  border: solid 4px transparent;
}
.popup-close:after, .popup-close:before {
  content: "";
  position: absolute;
  top: 11px;
  left: 5px;
  height: 4px;
  width: 16px;
  border-radius: 30px;
  background: #fff;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.popup-close:after {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.popup-close:hover {
  -webkit-transform: translate(50%, -50%) rotate(180deg);
  transform: translate(50%, -50%) rotate(180deg);
  background: #081d51;
  text-decoration: none;
  border-color: #081d51;
}
.popup-close:hover:after, .popup-close:hover:before {
  background: #fff;
  cursor: pointer;
}

