.fancy-radio label.radio {
  float: left;
  width: auto;
  line-height: 30px;
  margin: 0 0 10px 0;
  color: #fff;
  font-family: "Indie Flower", cursive;
  font-size: 22px;
  cursor: pointer;
  position: relative;
  clear: both;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
}
.fancy-radio label.radio:before {
  content: "";
  float: left;
  width: 29px;
  height: 29px;
  margin: 0 5px 0 0;
  background-color: #fff;
  border-radius: 100%;
  font-size: 22px;
  text-indent: 40px;
}
.fancy-radio label.radio:after {
  content: "";
  width: 5px;
  height: 5px;
  background-color: #2C3E50;
  border-radius: 100%;
  position: absolute;
  top: 12px;
  left: 12px;
  opacity: 0;
  transition: 0.2s;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
}
.fancy-radio label.radio.selected:after {
  width: 15.5px;
  height: 15.5px;
  top: 7px;
  left: 7px;
  opacity: 1;
}
.fancy-radio label.radio:hover {
  text-indent: 5px;
}

